import Vue from 'vue'
import VueRouter from 'vue-router'
import Dashboard from './Oauth/Dashboard.vue'
import Clients from './Oauth/Clients.vue'
import AuthorizedClients from './Oauth/AuthorizedClients.vue'
import PersonalAccessToken from './Oauth/PersonalAccessTokens.vue'

import Project from './Project/ProjectTable'
import ProjectDetail from './Project/ProjectDetail'
import ScopesTable from './Project/ScopeTable'

import ApprovedProject from './approval/ApprovedProjectList'
import AskableProjectList from './approval/AskableProjectList'
import PendingProjectList from './approval/PendingProjectList'
import Welcome from './Welcome.vue'


import "./app.sass"

//==============
// Vuejs addons
//==============
Vue.use(VueRouter)

//==============
// Standard libs
//==============
window._ = require('lodash');
window.Popper = require('popper.js').default;
window.Vue = require('vue');
/**
 * We'll load jQuery and the Bootstrap jQuery plugin which provides support
 * for JavaScript based Bootstrap features such as modals and tabs. This
 * code may be modified to fit the specific needs of your application.
 */

try {
    window.$ = window.jQuery = require('jquery');

    //require('bootstrap');
} catch (e) {}


function render () {


    /**
     * Next we will register the CSRF Token as a common header with Axios so that
     * all outgoing HTTP requests automatically have it attached. This is just
     * a simple convenience so we don't have to attach every token manually.
     */

    let token = document.head.querySelector('meta[name="csrf-token"]');

    if (token) {
        window.axios.defaults.headers.common['X-CSRF-TOKEN'] = token.content;
    } else {
        console.error('CSRF token not found: https://laravel.com/docs/csrf#csrf-x-csrf-token');
    }

    let auth = document.head.querySelector('meta[name="auth-token"]');

    if (auth) {
        window.axios.defaults.headers.common['Authorization'] = "Baerer "+auth.content;
    } else {
        console.error('Auth token not found');
    }
    console.log($.fn.modal)
//==============
// Routes definition
//==============
// 2. Define some routes
// Each route should map to a component. The "component" can
// either be an actual component constructor created via
// `Vue.extend()`, or just a component options object.
// We'll talk about nested routes later.
    const Parent = {
        data () {
            return {
                transitionName: 'slide-left'
            }
        },
        beforeRouteUpdate (to, from, next) {
            const toDepth = to.path.split('/').length
            const fromDepth = from.path.split('/').length
            this.transitionName = toDepth < fromDepth ? 'fade' : 'slide-left'
            next()
        },
        template: `
            <div class="parent">
              <transition :name="transitionName">
                <router-view class="container  child-view"></router-view>
              </transition>
            </div>
  `
    }
    const Main = {

        data () {
            return {
                transitionName: 'fade',
                depth: 1,
            }
        },
        ready() {
            this.prepareComponent();
        },

        /**
         * Prepare the component (Vue 2.x).
         */
        mounted() {
            this.prepareComponent();
        },
        methods:{
            prepareComponent(){
                var elem = document.querySelector('.sidenav');
                var instance = M.Sidenav.init(elem, {});
                instance.open()
            }
        },
        beforeRouteUpdate (to, from, next) {
            const toDepth = to.path.split('/').length
            const fromDepth = from.path.split('/').length
            this.depth = toDepth
            this.transitionName = toDepth < fromDepth ? 'fade' : 'slide-left'
            next()
        },
      template: `
      <div id="app" class="container">
        <ul id="slide-out" class="sidenav sidenav-fixed">
            <li class="bold"><a href="#!" v-if="this.depth > 1"><i class="material-icons">chevron_left</i></a></li>
            <li class="bold"><router-link class="waves-effect waves-teal" tag="a" to="/clients">Manage your api clients</router-link></li>
            <li class="bold"><router-link class="waves-effect waves-teal" tag="a" to="/ask">Available apis</router-link></li>
            <li class="bold"><router-link class="waves-effect waves-teal" tag="a" :to="{ name: 'project-list' }">Manage your apis</router-link></li>
        </ul>
        
        <!--<a href="#" data-target="slide-out" class="sidenav-trigger"><i class="material-icons">menu</i></a>-->
         <a href="#" data-target="slide-out" class="sidenav-trigger"><i class="material-icons">menu</i></a>

        <!-- route outlet -->
        <!-- component matched by the route will render here -->
        <main class="app-main-view parent">
          <transition :name="transitionName">
            <router-view class="container child-view"></router-view>
          </transition>
        </main>

      </div>
      `
    }
    const routes = [
      { path: '/', name:"main",          component : Welcome},
      { path: '/clients',   component: Dashboard },
      { path: '/approved', component: ApprovedProject },
      { path: '/ask',       component: AskableProjectList},
      { path: '/pending',  component: PendingProjectList },
      { path: '/projects', name: 'project-list',  component: Project },
      { path: '/projects/:id', name: 'project-detail',  component: ProjectDetail },
      /*{ path: '/projects',  component: Parent, children: [
          { path: '-list',     name: 'project-list',   component: Project},
          { path: '/:id', name: 'project-detail', component: ProjectDetail},
      ]}*/
    ]

// 3. Create the router instance and pass the `routes` option
// You can pass in additional options here, but let's
// keep it simple for now.
    const router = new VueRouter({
        routes // short for `routes: routes`
    })

// 4. Create and mount the root instance.
// Make sure to inject the router with the router option to make the
// whole app router-aware.

    const app = new Vue({
      router,
      template: '<Main/>',
      components: { Main }
    }).$mount('#mount')

}

/**
 * We'll load the axios HTTP library which allows us to easily issue requests
 * to our Laravel back-end. This library automatically handles sending the
 * CSRF token as a header based on the value of the "XSRF" token cookie.
 */
const axios = require('axios');
if(process.env.NODE_ENV === "production"){
    window.axios = axios.create({
        baseURL: window.BASE_URL || window.location.url,
        headers: {
            'X-Requested-With' : 'XMLHttpRequest'
        }
    })
    render()
}
else{


    axios.post("http://localhost:8000/login",{
        "credential":"thomas.ricci@cpnv.ch",
        "password": "toto"
    },{
        headers: {
            'X-Requested-With' : 'XMLHttpRequest'
        }
    }).then(resp => {
        console.log(resp)
        window.axios = axios.create({
            baseURL: 'http://localhost:8000',
            mode: 'no-cors',
            headers: {
                'X-Requested-With' : 'XMLHttpRequest',
                'Authorization': "Baerer "+resp.data,
                "Cookie": "token="+resp.data
            },
            withCredentials: true

        })
        render()
    })

}



